// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 600;
$btn-line-height           : 24px;
$btn-border-width          : 4px;
$btn-padding               : 8px 16px;
$btn-font-size             : 18px;
$btn-border-radius         : 4px;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR
// White
$btn-white-bg              : #FFF;
$btn-white-color           : #0B081B;
$btn-white-bg-hover        : #FFF;
$btn-white-color-hover     : #0B081B;

// Blue
$btn-blue-bg               : #0D0D58;
$btn-blue-color            : #FFF;
$btn-blue-bg-hover         : #0D0D58;
$btn-blue-color-hover      : #FFF;

// Black
$btn-black-bg              : #000;
$btn-black-color           : #FFF;
$btn-black-color-outline   : #121214;
$btn-black-bg-hover        : #121214;
$btn-black-color-hover     : #FFF;

// Link
$btn-link-bg               : transparent;
$btn-link-color            : #18A3FA;
$btn-link-bg-hover         : #F7F7F7;
$btn-link-color-hover      : darken(#18A3FA, 10%);

// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR

	// Black
	&--black {
		background-color : $btn-black-bg;
		color            : $btn-black-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}

	// White
	&--white {
		background-color : $btn-white-bg;
		color            : $btn-white-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg-hover;
			color            : $btn-white-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-white-bg;
				color            : $btn-white-color;
			}
		}
	}

	// Blue
	&--blue {
		background-color : $btn-blue-bg;
		color            : $btn-blue-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-blue-bg-hover;
			color            : $btn-blue-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-blue-bg;
				color            : $btn-blue-color;
			}
		}
	}

	// Link
	&--link {
		border         : none;
		color          : $white;
		font-size      : 12px;
		font-weight    : 600;
		letter-spacing : 1.5px;
		line-height    : 16px;
		padding        : 0;
		text-align     : left;
		text-transform : uppercase;
		white-space    : unset;

		&:hover,
		&:focus-within,
		&:active,
		&.active {

		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-link-bg;
				color            : $btn-link-color;
			}
		}
	}

	// Black
	&--outline-black {
		background-color : transparent;
		border           : 1px solid $btn-black-bg;
		color            : $btn-black-color-outline;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-black-bg;
				color            : $btn-black-color-outline;
			}
		}
	}

	// White
	&--outline-white {
		background-color : transparent;
		border           : 1px solid #CCC;
		padding          : 12px 24px;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg-hover;
			color            : $btn-white-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-white-bg;
			}
		}
	}


	// LOADING
	&--loading {
		&:before {
			animation        : loadingSpin 1s infinite linear;
			border           : 3px solid rgba($btn-loading-color, 0.6);
			border-radius    : 50%;
			border-top-color : $btn-loading-color;
			content          : '';
			display          : inline-block;
			height           : 12px;
			margin-left      : -15px;
			margin-right     : 10px;
			transition       : border .3s ease;
			vertical-align   : middle;
			width            : 12px;
		}

		&.outline {
			&:before {
				border           : 3px solid rgba($btn-loading-color-outline, 0.2);
				border-top-color : $btn-loading-color-outline;
			}

			&:hover {
				&:before {
					border           : 3px solid rgba($btn-loading-color, 0.6);
					border-top-color : $btn-loading-color;
				}
			}
		}
	}

	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
