* {
	-webkit-tap-highlight-color : rgba(0, 0, 0, 0);
}

html {
	box-sizing : border-box;
	height     : 100%;
}

*,
*::before,
*::after {
	box-sizing : inherit;
}

.page-wrapper {
	display        : flex;
	flex-direction : column;
	min-height     : 100%;
	overflow       : clip;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.main {
	flex : 1 0 auto;
}


.container {
	margin    : 0 auto;
	max-width : 1158px;
	padding   : 0 15px;
	width     : 100%;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-offset {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}


.section-head {

}

.section-title {
	color       : $black;
	font-size   : 22px;
	font-style  : normal;
	font-weight : 600;
	line-height : 30px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 38px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.tiny-text {
	color          : $black;
	font-size      : 12px;
	font-weight    : 600;
	letter-spacing : 1.5px;
	line-height    : 16px;
	text-transform : uppercase;
}

.scroll {
	height   : 100vh;
	overflow : hidden;
	position : relative;
	width    : 100vw;
}
