.tag {
  display: inline-block;
  margin: 5px 3px;
  position: relative;
}

.tag__input {
  width: 0; 
  height: 0; 
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  position: absolute;
  pointer-events: none;

  &:checked ~ .tag__btn {
    color: $white;
    background-color: $black;
  }
}

.tag__btn {
  display: block;
  font-weight: 600;
  color: $black;
  line-height: 20px;
  padding: 7px 16px;
  border-radius: 30px;
  border: 1px solid $black;
  cursor: pointer;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;

  &.is-active {
    color: $white;
    background-color: $black;
  }
}