.industries {
	background-color    : $blue;
	background-image    : url("../images/home/industries/industries-bg.jpg");
	background-position : top;
	background-repeat   : no-repeat;
	background-size     : cover;
	margin-top          : -130px;
	padding             : 241px 0 80px 0;

	@media screen and (min-width : $break-table) {
		margin-top : -160px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : -180px;
		padding    : 300px 0 120px 0;
	}
}

.industries__item {
	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {
		flex-direction : column;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}

	&:not(:last-child) {
		margin-bottom : 34px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 64px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}


	&:nth-child(1) {
		.industries__wrap {

			@media screen and (min-width : $break-table) {
				margin-bottom : -100px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : -80px;
			}
		}
	}

	&:nth-child(2) {
		.industries__wrap {

			@media screen and (min-width : $break-table) {
				margin-bottom: -124px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : -104px;
			}
		}
	}

	&:nth-child(3) {
		.industries__wrap {

			@media screen and (min-width : $break-table) {
				margin-bottom : -180px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : -155px;
			}
		}
	}

	&:nth-child(4) {
		.industries__wrap {

			@media screen and (min-width : $break-table) {
				margin-bottom : -180px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : -155px;
			}
		}
	}
}

.industries__wrap {
	margin-top : 24px;

	@media screen and (min-width : $break-table) {
		margin-top : 0;
	}
}


.industries__title {
	color         : $white;
	font-size     : 22px;
	font-weight   : 600;
	line-height   : 30px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 32px;
		line-height   : 38px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.industries__text {
	color       : $white;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		max-width : 356px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 556px;
	}
}

.tiny-text--industries {
	color         : $white;
	margin-bottom : 24px;
}
