  font-size: 20px;
  font-weight: 600;
  line-height: 120%;
  max-width: 290px;
}
@media screen and (min-width: 744px) {
  .map__title {
    font-size: 24px;
    line-height: 125%;
    max-width: 337px;
  }
}
@media screen and (min-width: 1081px) {
  .map__title {
    font-size: 32px;
    max-width: 437px;
  }
}

.map__img {
  text-align: center;
}

.clients-list {
  background-image: url("../images/media/media-bg.svg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 48px 0;
}
@media screen and (min-width: 744px) {
  .clients-list {
    background-size: 2200px;
    padding: 80px 0;
  }
}
@media screen and (min-width: 1081px) {
  .clients-list {
    background-size: contain;
    padding: 120px 0;
  }
}

.clients-list__item {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(4, 1fr);
  padding: 24px 0;
  position: relative;
}
@media screen and (min-width: 744px) {
  .clients-list__item {
    grid-gap: 16px;
    grid-template-columns: repeat(12, 1fr);
    padding: 48px 0;
  }
}
@media screen and (min-width: 1081px) {
  .clients-list__item {
    grid-gap: 24px;
  }
}
.clients-list__item:first-child {
  border-bottom: 1px solid #CCC;
  border-top: 1px solid #CCC;
}
.clients-list__item:not(:first-child) {
  border-bottom: 1px solid #CCC;
}
.clients-list__item:hover .tiny-text--list {
  color: #0D0D58;
}
.clients-list__item:hover .clients-list__text {
  color: #0D0D58;
}
.clients-list__item:hover .clients-list__icon svg path {
  stroke: #0D0D58;
}

.clients-list__img {
  grid-column: 4 span;
}
@media screen and (min-width: 744px) {
  .clients-list__img {
    grid-column: 1/4;
  }
}
.clients-list__wrap {
  grid-column: 4 span;
}
@media screen and (min-width: 744px) {
  .clients-list__wrap {
    grid-column: 4/11;
  }
}
.tiny-text--list {
  color: #000;
  display: none;
  font-weight: 600;
  transition: color 0.3s ease;
}
@media screen and (min-width: 744px) {
  .tiny-text--list {
    display: block;
    margin-bottom: 16px;
  }
}
@media screen and (min-width: 1081px) {
  .tiny-text--list {
    margin-bottom: 24px;
  }
}

.clients-list__text {
  color: #000;
  font-size: 18px;
  font-weight: 600;
  line-height: 133.333%;
  transition: color 0.3s ease;
}
@media screen and (min-width: 1081px) {
  .clients-list__text {
    font-size: 24px;
  }
}

.clients-list__icon {
  position: absolute;
  right: 0;
  top: 32px;
}
@media screen and (min-width: 744px) {
  .clients-list__icon {
    top: 48px;
  }
}
.clients-list__icon svg path {
  stroke: rgba(0, 0, 0, 0.5);
  transition: stroke 0.3s ease;
}

.works {
  background-image: url("../images/ourwork/works/w-foil.svg");
  background-size: 100% auto;
  background-position: top center;
}
@media screen and (max-width: 1081px) {
  .works {
    padding-bottom: 70px;
  }
}
@media screen and (max-width: 744px) {
  .works {
    padding-bottom: 50px;
  }
}

.works__filter {
  display: flex;
  align-items: flex-start;
  margin-bottom: 80px;
  padding-top: 40px;
  border-top: 1px solid #cccccc;
}
@media screen and (max-width: 1081px) {
  .works__filter {
    margin-bottom: 50px;
  }
}
@media screen and (max-width: 744px) {
  .works__filter {
    display: block;
    padding-top: 30px;
    margin-bottom: 35px;
  }
}

.works__title {
  margin-right: 75px;
  margin-top: 4px;
}
@media screen and (max-width: 1081px) {
  .works__title {
    font-size: 18px;
  }
}
@media screen and (max-width: 744px) {
  .works__title {
    font-size: 18px;
    margin-bottom: 15px;
    margin-top: 0;
    margin-right: 0;
  }
}

@media screen and (max-width: 744px) {
  .works__list {
    margin-left: -6px;
  }
}

.works__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 64px;
  grid-column-gap: 24px;
}
@media screen and (max-width: 1081px) {
  .works__grid {
    grid-row-gap: 48px;
    grid-column-gap: 16px;
  }
}
@media screen and (max-width: 744px) {
  .works__grid {
    display: block;
  }
}

.cursor {
  display: none;
}

.cursor--plus {
  display: block;
  position: fixed;
  pointer-events: none;
  width: 80px;
  height: 80px;
  top: -50px;
  left: -50px;
  z-index: 1;
  border-radius: 50px;
  background-color: #0D0D58;
  color: #FFF;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  opacity: 0;
}