.services-about {
	background-image  : url("../images/home/about/about-bg.svg");
	background-repeat : no-repeat;
	background-size   : cover;
	padding           : 48px 0 96px 0;

	@media screen and (min-width : $break-table) {
		padding : 48px 0 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 114px 0 120px 0;
	}
}

.services-about__head {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}

	.tiny-text {
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 24px;
		}
	}
}

.services-about__img {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.services-about__text {
	color       : $black;
	font-size   : 18px;
	font-weight : 600;
	line-height : 133%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size : 24px;
		max-width : 919px;
	}
}
