.industries-about {
	background-color    : #0D0D58;
	background-image    : url("../images/home/industries/industries-bg.jpg");
	background-position : top;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 80px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}

	.tiny-text--about {
		color         : $white;
		margin-bottom : 24px;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 48px;
		}
	}
}

.industries-about__wrapper {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 64px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}
}

.industries-about__item {
	grid-column : 4 span;
	position    : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}


	@media screen and (min-width : $break-desktop) {
		grid-column    : 6 span;
		padding-bottom : 0;
	}

	&:nth-child(1) {
		@media screen and (min-width : $break-table) {
			padding-bottom : 60px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-bottom : 0;
		}
	}

	&:nth-child(2) {
		@media screen and (min-width : $break-table) {
			padding-bottom : 80px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-bottom : 0;
		}
	}

	&:nth-child(3) {
		@media screen and (min-width : $break-table) {
			padding-bottom : 70px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-bottom : 0;
		}
	}

	&:nth-child(4) {
		@media screen and (min-width : $break-table) {
			padding-bottom : 120px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-bottom : 0;
		}
	}
}

.industries-about__wrap {
	display        : flex;
	flex-direction : column-reverse;
	margin-bottom  : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 51px;
		min-height    : 80px;
		position      : relative;
	}
}

.industries-about__img {
	margin-bottom : 24px;
	width         : 100%;

	@media screen and (min-width : $break-table) {
		bottom   : 0;
		position : absolute;
	}

	@media screen and (min-width : $break-desktop) {
		bottom        : 0;
		margin-bottom : -20px;
		right         : 0;
	}

	img {
		width : 100%;
	}
}

.industries-about__title {
	color       : $white;
	font-size   : 40px;
	font-weight : 600;
	line-height : 120%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
	}
}

.industries-about__text {
	color       : $white;
	font-size   : 14px;
	font-weight : 400;
	line-height : 142%;

	@media screen and (min-width : $break-table) {
		max-width : 392px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 150%;
		max-width   : none;
	}
}
