.map {
	background-image    : url("../images/contacts/bg-contacts.jpg");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 20px 0 60px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-size : 100% 1080px;
		padding         : 100px 0;
	}
}

.tiny-text--map {
	color         : $white;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 12px;
	}
}

.map__wrap {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 20px;
	}
}

.map__title {
	color       : #FFF;
	font-size   : 20px;
	font-weight : 600;
	line-height : 120%;
	max-width   : 290px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 125%;
		max-width   : 337px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
		max-width : 437px;
	}
}

.map__img {
	text-align : center;
}
