.capabilities {
	padding-top : 48px;

	@media screen and (min-width : $break-table) {
		padding-top : 80px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-top : 115px;
	}
}

.capabilities__wrapper {
	border-bottom  : 0.4px solid $black;
	padding-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 68px;
	}
}

.capabilities__head {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 68px;
	}

	.tiny-text {
		margin-bottom : 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 24px;
		}
	}
}

.capabilities__title {
	color       : $black;
	font-size   : 22px;
	font-weight : 600;
	line-height : 136%;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 125%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
	}
}

