.services-item {
	padding : 120px 0;
}

.services-item--blue {
	background-color    : $blue;
	background-image    : url("../images/services/items/bg-blue.jpg");
	background-position : center top;
	background-repeat   : no-repeat;
	background-size     : cover;
}

.services-item--white {
	background-color    : $white;
	background-image    : url("../images/services/items/bg-white.svg");
	background-position : center top;
	background-repeat   : no-repeat;
	background-size     : cover;
}

.services-item__grid {
	display               : grid;
	grid-row-gap          : 48px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}
}

.services-item__left {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		height    : 100%;
		left      : 0;
		max-width : 430px;
		position  : sticky;
		top       : 175px;
		z-index   : 1;
	}
}

.services-item__title {
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.services-item__title--blue {
	color : $white;
}

.services-item__title--black {
	color : $black;
}

.services-item__text {
	font-size   : 14px;
	font-weight : 400;
	line-height : 142%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		margin-bottom : 64px;
	}
}

.services-item__text--blue {
	color : $white;
}

.services-item__text--black {
	color : $black;
}

.services-item__img {
	margin-bottom : 48px;
	order         : -1;
	text-align    : center;
	width         : 100%;
	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		order         : 1;
		text-align    : left;
	}
}

.tiny-text--white {
	color         : $white;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.tiny-text--black {
	color         : $white;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.tiny-text--black {
	color         : $black;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.services-item__right {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7/13;
		max-width   : 552px;
	}
}

.services-item__top {
	margin-bottom : 16px;
	padding-top   : 16px;
}

.services-item__bottom {

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.services-item__subtitle--black {
	color : $black;
}

.services-item__name {
	color         : $white;
	font-size     : 24px;
	font-weight   : 600;
	line-height   : 133%;
	margin-bottom : 16px;
}

.services-item__name--black {
	color : $black;
}

.services-item__about {
	color       : $white;
	font-size   : 14px;
	font-weight : 400;
	line-height : 142%;
}

.services-item__about--black {
	color : $black;
}

.services-item__subtitle {
	color         : $white;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.services-item__subtitle--black {
	color : $black;
}

.services-item__list {
	li {
		color       : $white;
		font-size   : 14px;
		font-weight : 600;
		line-height : 142%;

		&:not(:last-child) {
			margin-bottom : 8px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 0;
			}
		}

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			border-bottom : 1px solid $white;
			padding       : 16px 0;
		}

		&:first-child {


			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				border-top : 1px solid $white;
			}
		}
	}
}


.services-item__list--double {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display               : grid;
		grid-column-gap       : 24px;
		grid-template-columns : repeat(6, 1fr);
	}

	li {
		grid-column : 3 span;

		&:nth-child(2) {

			@media screen and (min-width : $break-desktop) {
				border-top : 1px solid $white;
			}
		}
	}
}


.services-item__list--black {
	li {
		color : $black;


		@media screen and (min-width : $break-desktop) {
			border-bottom : 1px solid #CCC;
		}

		&:first-child {

			@media screen and (min-width : $break-desktop) {
				border-top : 1px solid #CCC;
			}
		}
	}
}

.services-item__list--black.services-item__list--double {

	li {

		&:nth-child(2) {

			@media screen and (min-width : $break-desktop) {
				border-top : 1px solid #CCC;
			}
		}
	}
}


.services-item__slider {
	margin-top : 64px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 80px;
	}

	.swiper-nav {
		display         : none;
		gap             : 24px;
		justify-content : flex-end;
		margin-bottom   : 32px;
		@media screen and (min-width : $break-table) {
			display : flex;
		}
	}
}

.services-item__slider--black {
	.swiper-button {
		svg {
			path {
				fill : $black
			}
		}
	}
}

.swiper--services {
	overflow : visible;

	.swiper-slide {
		width : 75%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 50%;
		}
	}
}

.services-slide {
	margin : 0 8px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 12px;
	}
}

.services-slide__img {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.services-slide__name {
	color         : $white;
	margin-bottom : 12px;
}

.services-slide__text {
	color       : $white;
	font-size   : 14px;
	font-weight : 600;
	line-height : 142%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 18px;
		line-height : 133%;
	}
}

.services-slide--black {
	.services-slide__name {
		color : $black;
	}

	.services-slide__text {
		color : $black;
	}
}
