
.work-item {
  display: none;

  &.is-show,
  &.is-active {
    display: block;
  }

  @media screen and (max-width : $break-table) {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
.work-item__title {
  margin-top: 24px;

  @media screen and (max-width : $break-desktop) {
    margin-top: 16px;
  }
}

.work-item__image {
  @media screen and (min-width : $break-desktop) {
    display: block;
    overflow: hidden;

    img {
      transition: 0.5s ease;
      transform-origin: 50% 0%;
    }

    &:hover {
      img {
        transform: scale(1.03) translateY(-10px);
      }
    }
  }
}