.about-page {
	background-image    : url("../images/about/about/about-bg.svg");
	background-position : top center;
	background-repeat   : no-repeat;
	background-size     : 100% 1485px;
	padding             : 48px 0 80px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 115px 0 0 0;
	}
}

.about-page__head {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.tiny-text--about {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.about-page__title {
	color       : $black;
	font-size   : 22px;
	font-weight : 600;
	line-height : 136%;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 125%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
	}
}

.about-page__gallery {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 48px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap      : 24px;
		margin-bottom : 120px;
	}
}

.about-page__img {
	grid-column : 4 span;
	position    : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}

	&:last-child {
		min-height : 400px;
	}
}

.values {
	align-items           : flex-end;
	display               : grid;
	grid-row-gap          : 32px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.values__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/6;
	}
}

.values__title {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.values__item {

	&:not(:last-child) {
		margin-bottom : 32px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 48px;
		}
	}
}

.values__name {
	color         : $black;
	font-size     : 16px;
	font-weight   : 600;
	line-height   : 133%;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size : 18px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.values__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 142%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		max-width : 457px;
	}
}

.values__right {
	grid-column : 4 span;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 8/13;
		text-align  : unset;
	}

	img {


		@media screen and (min-width : $break-desktop) {
			width : 100%;
		}
	}
}

.tabs__content {
	display : none;

	&.active {
		display : block;
	}
}
