.expertise {
	background-image  : url("../images/home/about/about-bg.svg");
	background-repeat : no-repeat;
	background-size   : cover;
	padding-bottom    : 80px;
	padding-top       : 80px;

	@media screen and (min-width : $break-table) {
		padding-top : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 0;
		padding-top    : 120px;
	}
}

.expertise__head {
	margin-bottom : 50px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 35px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 80px;
	}

	.tiny-text {
		margin-bottom : 24px;
	}
}

.expertise__title {

	@media screen and (min-width : $break-table) {
		max-width : 1023px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.expertise__wrapper {
	display               : grid;
	grid-gap              : 50px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 64px;
	}
}

.expertise-item {
	grid-column : 4 span;
	position    : relative;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		align-items : center;
		display     : flex;
		grid-column : 12 span;
		text-align  : left;
	}

	@media screen and (min-width : $break-desktop) {
		display     : block;
		grid-column : 4 span;
		text-align  : center;

		&:not(:first-child) {
			&:after {
				background-color : rgba(0, 0, 0, 0.2);
				content          : "";
				height           : 65%;
				left             : -30px;
				position         : absolute;
				top              : 0;
				width            : 1px;
			}
		}

	}

	&:nth-of-type(2) {

		@media screen and (min-width : $break-table) {
			flex-direction : row-reverse;
		}

		.expertise-item__top {
			@media screen and (min-width : $break-table) {
				margin-bottom : 0;
				margin-left   : 120px;
				margin-right  : 0;
				max-width     : 320px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 85px;
				margin-left   : 0;
				margin-right  : 0;
				max-width     : none;
			}
		}
	}
}

.expertise-item__top {
	margin-bottom : 35px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		margin-right  : 120px;
		max-width     : 320px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 85px;
		margin-right  : 0;
		max-width     : none;
	}
}

.expertise-item__icon {
	margin-bottom : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.expertise-item__wrap {
	align-items   : center;
	display       : flex;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		display : block;
	}
}

.expertise-item__name {
	color       : $black;
	font-size   : 22px;
	font-weight : 600;
	line-height : 30px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}
}

.expertise-item__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	text-align  : left;
}

.expertise-item__img {
	height : 490px;
	margin : 0 auto;
	width  : 242px;
}

.expertise-item__img--iphone {
	background-image : url("../images/home/expertise/data-1.png");
	overflow         : hidden;
	position         : relative;
	z-index          : 2;
}

.graphics__item {
	border-radius : 40px;
	left          : 50%;
	opacity       : 0;
	overflow      : hidden;
	position      : absolute;
	top           : 51%;
	transform     : translate(-50%, -50%);
	transition    : opacity .6s ease-in, visibility .6s ease-in;
	width         : 220px;
	z-index       : -1;

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.expertise-item__img--scroll {
	border-radius : 40px;
	overflow      : hidden;
	position      : relative;
	z-index       : 2;
}

.iphone-scroll {
	left     : 0;
	position : absolute;
	top      : 0;
	width    : 100%;
	z-index  : -1;
}

.iphone-scroll__item {
	margin     : 0 auto;
	max-height : 1000px;
	width      : 219px;

	&::-webkit-scrollbar {

	}

	&::-webkit-scrollbar {
		background-color : transparent;
		height           : 0px;
		width            : 0px;
	}

	&::-webkit-scrollbar-thumb {
		background    : transparent;
		border-radius : 10px;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}
