.header {
	left     : 0;
	position : absolute;
	top      : 20px;
	width    : 100%;
	z-index  : 101;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		top : 65px;
	}
}

.header--light {
	background-color : $white;
	left             : 0;
	padding-top      : 1px;
	position         : fixed;
	right            : 0;
	top              : -200px;
	transform        : translateY(200px);
	transition       : background-color .3s ease, transform .45s ease;
	width            : 100%;

	.nav__link {

		@media screen and (min-width : $break-table) {
			color : $black;
		}
	}

	.header__wrapper {
		align-items : center;
		padding     : 16px 0;

		@media screen and (min-width : $break-table) {
			padding : 20px 0;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 24px 0;
		}

	}

	.header__logo {
		height : 40px;
		width  : 102px;

		@media screen and (min-width : $break-desktop) {
			height : 44px;
			width  : 122px;
		}

		svg {
			height : 40px;
			width  : 102px;

			@media screen and (min-width : $break-desktop) {
				height : 44px;
				width  : 122px;
			}

			path {
				//fill : $blue;
			}

			circle {
				fill: #231BA1;
			}

			defs {
				stop {
					&:nth-of-type(1) {
						stop-color: #3522A3;
					}

					&:nth-of-type(2) {
						stop-color: #000017;
					}

					&:nth-of-type(3) {
						stop-color: #FF75D8;
					}

					&:nth-of-type(4) {
						stop-color: #6E3CD0;
					}
				}
			}
		}
	}

	.header__logo.header__logo--mobile {
		svg {
			circle {
				fill: #fff;
			}

			defs {
				stop {
					stop-color: #fff;
				}
			}
		}
	}

	.header__menu {
		@media screen and (min-width : $break-desktop) {
			padding-top: 0;
		}
	}

	.b-btn--white {
		background-color : $black;
		color            : $white;
	}

	.burger {
		color : $black;
	}
}
