.header--contact {
	background-image  : url("../images/icon/header-bg.jpg");
	background-repeat : no-repeat;
	background-size   : cover;
	padding           : 16px 0;
	position          : absolute;
	top: 0;

	@media screen and (min-width : $break-table) {
		padding : 20px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 0;
	}

	.header__wrapper {
		align-items : center;
		display     : flex;
	}

	.header__logo {
		height : 47px;
		width  : 120px;

		@media screen and (min-width : $break-table) {
			height : 54px;
			width  : 140px;
		}

		@media screen and (min-width : $break-desktop) {
			height : 75px;
			width  : 192px;
		}

		svg {
			height     : 47px;
			transition : width .3s ease, height .3s ease;
			width      : 120px;

			@media screen and (min-width : $break-table) {
				height : 54px;
				width  : 140px;
			}

			@media screen and (min-width : $break-desktop) {
				height : 75px;
				width  : 192px;
			}


		}
	}
}

.header__logo {
	height  : 40px;
	width   : 102px;
	z-index : 4;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 129px;
		width  : 330px;
	}

	svg {
		height     : 47px;
		transition : width .3s ease, height .3s ease, visibility .3s ease, opacity .3s ease;
		width      : 120px;

		@media screen and (min-width : $break-table) {
			height : 54px;
			width  : 140px;
		}

		@media screen and (min-width : $break-desktop) {
			height : 129px;
			width  : 330px;
		}

		circle {
			fill: $white;
		}

		defs {
			stop {
				stop-color: #fff;
			}
		}
	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin          : 0 auto;
	max-width       : 1160px;

	@media screen and (min-width : $break-desktop) {
		align-items : flex-start;
	}
}

.header__menu {
	background-color : $blue;
	display          : flex;
	flex-direction   : column;
	height           : 100dvh;
	left             : -100%;
	position         : fixed;
	top              : 0;
	width            : 100%;
	z-index          : 2;

	&.active {
		left : 0;
		transition: left .3s ease;
	}

	@media screen and (min-width : $break-table) {
		background-color : transparent;
		height           : auto;
		left             : 0;
		margin-left      : auto;
		position         : unset;
		top              : 0;
		width            : auto;
		z-index          : 1;
	}

	@media screen and (min-width : $break-desktop) {
		padding-top: 8px;
	}

}

.header__btns {
	align-items : center;
	display     : flex;

	@media screen and (min-width : $break-table) {
		margin-left : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 40px;
	}

	.b-btn--white {
		margin-left : 16px;
		padding     : 4px 13px;

		@media screen and (min-width : $break-table) {
			display : inline-flex;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.header__wrap {
	height     : 100%;
	padding    : 0 16px 46px 16px;
	overflow-y: auto;
	margin-top: 86px;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		flex-direction  : row;
		justify-content : unset;
		min-height      : auto;
		padding         : 0;
		margin-top: 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.header__chat {
	@media screen and (min-width : $break-table) {
		display : none;
	}

	a {
		color       : $white;
		display     : block;
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;

		&:not(:last-child) {
			margin-bottom : 10px;
		}
	}
}

.header__name {
	color         : $white;
	font-size     : 22px;
	font-weight   : 600;
	line-height   : 30px;
	margin-bottom : 16px;
}

.nav__list {
	display        : flex;
	flex-direction : column;
	margin-bottom  : 80px;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		flex-direction : row;
		margin-bottom  : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.nav__item {
	& + & {

		@media screen and (min-width : $break-table) {
			margin-left : 23px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:not(:last-child) {
		margin-bottom : 32px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}
}

.nav__link {
	color       : $white;
	font-size   : 22px;
	font-weight : 600;
	line-height : 30px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 18px;
		line-height : 24px;
	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $white;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 20px;
	padding          : 0;
	position         : relative;
	width            : 24px;

	@media screen and (min-width : $break-table) {
		display : none;
	}


	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		left  : unset;
		right : 0;
		top   : calc(100% - 2px);
		width : 50%;
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
			width      : 100%;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}

.menu-close {
	background-color : transparent;
	border           : none;
	position         : absolute;
	right            : 16px;
	top              : 28.5px;
	z-index          : 100;
	@media screen and (min-width : $break-table) {
		display : none;
	}
}
