.services {
	background-image    : url("../images/home/about/about-bg.svg");
	background-position : top;
	background-repeat   : no-repeat;
	background-size     : contain;
	padding-top         : 120px;
	position            : relative;
	z-index             : 1;
}

.services__head {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.tiny-text {
		margin-bottom : 24px;
	}
}

.services__title {
	max-width : 813px;
}

.services__wrapper {
	background-color : $white;
	border-radius    : 8px;
	box-shadow       : 0 8px 16px 0 rgba(0, 0, 0, 0.08), 0 0 4px 0 rgba(0, 0, 0, 0.04);
	counter-reset    : count;
	padding          : 32px 24px;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 64px 64px;
	}

	.b-btn--outline-white {
		left         : 50%;
		margin-right : auto;
		margin-top   : 48px;
		position     : relative;
		transform    : translateX(-50%);

		@media screen and (min-width : $break-table) {
			left       : 100%;
			margin-top : 64px;
			transform  : translateX(-100%);
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.accordion--services {

	.accordion__control {
		position : relative;

		&:before {
			color             : rgba(0, 0, 0, 0.5);
			content           : "0" counter(count);
			counter-increment : count;
			font-size         : 12px;
			font-weight       : 500;
			left              : 0;
			letter-spacing    : 1.26px;
			line-height       : 18px;
			position          : absolute;
			top               : 0;
			z-index           : 1;

			@media screen and (min-width : $break-table) {
				font-size   : 14px;
				line-height : 20px;
				top         : 5px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	.accordion__title {
		padding-left : 35px;
		text-align: left;
	}
}

.services__wrap {

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.services__about {
	opacity    : 0;
	transform  : translateX(-50px);
	transition : opacity .4s ease, transform .4s ease;

	@media screen and (min-width : $break-table) {
		max-width : 296px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 456px;
	}

	&.active {
		opacity   : 1;
		transform : translateX(0);
	}
}

.services__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:not(:last-child) {
		margin-bottom : 24px;
	}
}

.services__list {
	margin       : 0 0 24px 0;
	opacity      : 0;
	padding-left : 25px;
	transform    : translateX(-50px);
	transition   : opacity .2s ease, transform .2s ease;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	li {
		color       : $black;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;


		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:last-child) {
			margin-bottom : 12px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 16px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&.active {
		opacity   : 1;
		transform : translateX(0);
	}

}


.services__link {
	color       : $black;
	font-size   : 16px;
	font-weight : 600;
	line-height : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 18px;
		line-height : 24px;
	}
}
