.works {
  background-image: url('../images/ourwork/works/w-foil.svg');
  background-size: 100% auto;
  background-position: top center;
  @media screen and (max-width : $break-desktop) {
    padding-bottom: 70px;
  }
  @media screen and (max-width : $break-table) {
    padding-bottom: 50px;
  }
}
.works__filter {
  display: flex;
  align-items: flex-start;
  margin-bottom: 80px;
  padding-top: 40px;
  border-top: 1px solid $gray2;

  @media screen and (max-width : $break-desktop) {
    margin-bottom: 50px;
  }

  @media screen and (max-width : $break-table) {
    display: block;
    padding-top: 30px;
    margin-bottom: 35px;
  }
}

.works__title {
  margin-right: 75px;
  margin-top: 4px;

  @media screen and (max-width : $break-desktop) {
    font-size: 18px;
  }
  @media screen and (max-width : $break-table) {
    font-size: 18px;
    margin-bottom: 15px;
    margin-top: 0;
    margin-right: 0;
  }
}

.works__list {
  @media screen and (max-width : $break-table) {
    margin-left: -6px;
  }
}

.works__grid {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-row-gap: 64px;
  grid-column-gap: 24px;

  @media screen and (max-width : $break-desktop) {
    grid-row-gap: 48px;
    grid-column-gap: 16px;
  }
  @media screen and (max-width : $break-table) {
    display: block;
  }
}

.cursor {
  display: none;
}
.cursor--plus {
  display: block;
  position: fixed;
  pointer-events: none;
  width: 80px;
  height: 80px;
  top: -50px;
  left: - 50px;
  z-index: 1;
  border-radius: 50px;
  background-color: $blue;
  color: $white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  opacity: 0;
}