.footer {
	background-color : $blue;
	padding          : 64px 0;

	@media screen and (min-width : $break-table) {
		padding : 80px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer--contact {
	background-color : #1D1D1D;

	.footer__link {
		color : $gray;
	}

	.footer__name {
		color : $gray;
	}

	.b-btn {
		color : $gray;
	}

	.s-block {
		&__link {
			svg {
				path {
					fill : $gray;
				}
			}
		}
	}
}

.footer__top {
	margin-bottom : 64px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 80px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__item {

	&:not(:last-child) {
		margin-bottom : 48px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}


	ul {
		li {
			&:not(:last-child) {
				margin-bottom : 8px;

				@media screen and (min-width : $break-table) {
					margin-bottom : 12px;
				}

				@media screen and (min-width : $break-desktop) {

				}
			}
		}
	}
}

.footer__link {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;
}

.footer__name {
	color         : $white;
	font-size     : 16px;
	font-weight   : 600;
	line-height   : 24px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		line-height   : 40px;
		margin-bottom : 24px;
	}
}

.footer__bottom {
	.b-btn {
		display         : flex;
		justify-content : flex-start;

		@media screen and (min-width : $break-table) {
			display : inline-flex;
		}

		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 0;
			}
		}
	}

	.b-btn + .b-btn {
		margin-left : 0;

		@media screen and (min-width : $break-table) {
			margin-left : 64px;
		}
	}
}
