.team {
	padding : 56px 0 80px 0;

	@media screen and (min-width : $break-table) {
		padding : 88px 0 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 55px 0 120px 0;
	}
}



.team__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}

	.swiper-button {
		svg {
			path {
				fill : $black;
			}
		}
	}
}

.swiper-nav--team {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.team__wrapper {
	margin : 0 -15px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -12px;
	}
}

.swiper--team {
	overflow : visible;

	.swiper-slide {
		width : 75%;

		@media screen and (min-width : $break-table) {
			width : 38%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 25%;
		}

		.team-item {
			margin : 0 8px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin : 0 12px;
			}
		}

		&:first-child {
			.team-item {
				margin-left : 15px;

				@media screen and (min-width : $break-desktop) {
					margin-left : 12px;
				}
			}
		}

		&:last-child {
			.team-item {
				margin-right : 15px;

				@media screen and (min-width : $break-desktop) {
					margin-right : 12px;
				}
			}
		}
	}
}
