.clients-list {
	background-image    : url("../images/media/media-bg.svg");
	background-position : top center;
	background-repeat   : no-repeat;
	background-size     : contain;
	padding             : 48px 0;

	@media screen and (min-width : $break-table) {
		background-size : 2200px;
		padding         : 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
		background-size : contain;
		padding         : 120px 0;
	}
}

.clients-list__grid {

}

.clients-list__item {
	display               : grid;
	grid-gap              : 20px;
	grid-template-columns : repeat(4, 1fr);
	padding               : 24px 0;
	position              : relative;

	@media screen and (min-width : $break-table) {
		grid-gap              : 16px;
		grid-template-columns : repeat(12, 1fr);
		padding               : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 24px;
	}

	&:first-child {
		border-bottom : 1px solid #CCC;
		border-top    : 1px solid #CCC;
	}

	&:not(:first-child) {
		border-bottom : 1px solid #CCC;
	}

	&:hover {
		.tiny-text--list {
			color : $blue;
		}

		.clients-list__text {
			color : $blue;
		}

		.clients-list__icon {
			svg {
				path {
					stroke : $blue;
				}
			}
		}
	}

}

.clients-list__img {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/4;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.clients-list__wrap {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 4/11;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.clients-list__img {

}

.tiny-text--list {
	color       : $black;
	display     : none;
	font-weight : 600;
	transition  : color .3s ease;

	@media screen and (min-width : $break-table) {
		display       : block;
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.clients-list__text {
	color       : $black;
	font-size   : 18px;
	font-weight : 600;
	line-height : 133.333%;
	transition  : color .3s ease;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size : 24px;
	}
}

.clients-list__icon {
	position : absolute;
	right    : 0;
	top      : 32px;

	@media screen and (min-width : $break-table) {
		top : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	svg {
		path {
			stroke     : rgba(0, 0, 0, 0.5);
			transition : stroke .3s ease;
		}
	}
}
