.services-wrapper {


}

.services-nav {
	background-color : #FFF;
	counter-reset    : count;
	//padding          : 16px 0;
	margin           : 0 -16px;
	position         : sticky;
	top              : 73px;
	z-index          : 4;

	@media screen and (min-width : $break-table) {
		top : 81px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0;
		top    : 93px;
	}

	&.active {
		border-top : 1px solid $gray;
	}
}

.services-nav__list {
	align-items     : center;
	display         : flex;
	flex-wrap       : nowrap;
	gap             : 48px;
	justify-content : space-between;
	overflow-x      : auto;
	padding         : 0 16px;
	@media screen and (min-width : $break-table) {
		gap : 62px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}
}

.services-nav__item {
	flex-shrink : 0;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.services-nav__link {
	color       : $black;
	display     : flex;
	font-size   : 18px;
	font-weight : 600;
	line-height : 133%;
	padding     : 16px 0;
	position    : relative;


	@media screen and (min-width : $break-table) {
		padding : 24px 0;
	}

	&:before {
		color             : rgba(0, 0, 0, .5);
		content           : counter(count);
		counter-increment : count;
		font-size         : 12px;
		font-weight       : 600;
		letter-spacing    : 1.5px;
		line-height       : 133%;
		padding-right     : 8px;
		position          : relative;
		text-transform    : uppercase;
		top               : 2px;
		z-index           : 1;
	}

	&:after {
		background-color : $black;
		bottom           : 0px;
		content          : "";
		height           : 2px;
		left             : 0;
		opacity          : 0;
		position         : absolute;
		transition       : width .3s ease, opacity .3s ease, visibility .3s ease;
		visibility       : hidden;
		width            : 0;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&.active {
		&:after {
			opacity    : 1;
			visibility : visible;
			width      : 100%;
		}
	}
}
