.marquee {
	padding : 24px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 0;
	}
}

.swiper--marquee {
	position : relative;
	width    : 100%;

	.swiper-wrapper {
		position                           : relative;
		-webkit-transition-timing-function : linear !important;
		transition-timing-function         : linear !important;
	}

	.swiper-slide {
		width : 50%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 15%;
		}
	}
}

.pieContainer {
	color    : red;
	height   : 120px;
	position : relative;
	width    : 120px;

	.test {
		position : absolute;
		top      : 120px;
		z-index  : 1;
	}
}

.pieBackground {
	//border-radius : 100%;
	//box-shadow    : 0px 0px 8px rgba(0, 0, 0, 0.5);
	height   : 150px;
	position : absolute;
	width    : 120px;
}

.pie {
	border-radius : 100%;
	clip          : rect(0px, 72px, 150px, 0px);
	height        : 113px;
	position      : absolute;
	transition    : all 1s;
	width         : 113px;
}

.hold {
	border-radius : 100%;
	clip          : rect(0px, 150px, 150px, 75px);
	height        : 113px;
	position      : absolute;
	width         : 113px;
}


#pieSlice2 {
	transform : rotate(106deg);
}

#pieSlice2 .pie {
	background-color : #0D0D58;
	transform        : rotate(60deg);
}

#pieSlice1 {
	transform : rotate(16deg);
}

#pieSlice1 .pie {
	background-color : #020224;
	transform        : rotate(230deg);
}

#pieSlice4 {
	transform : rotate(210deg);
}

#pieSlice4 .pie {
	background-color : #08F;
	transform        : rotate(10deg);
}

#pieSlice3 {
	transform : rotate(165deg);
}

#pieSlice3 .pie {
	background-color : #593FF0;
	transform        : rotate(145deg);
}

#pieSlice4 {
	transform : rotate(308deg);

	&.pie {
		transform : rotate(84deg);
	}
}

#pieSlice4 .pie {
	background-color : #D56BC4;
	transform        : rotate(92deg);
}

.innerCircle {
	background-color : white;
	border-radius    : 100%;
	//box-shadow       : 0px 0px 8px rgba(0, 0, 0, 0.5) inset;
	color            : white;
	height           : 102px;
	left             : 6px;
	position         : absolute;
	top              : 6px;
	width            : 102px;
}

.innerCircle .content {
	display    : block;
	font-size  : 14px;
	left       : 0;
	position   : absolute;
	text-align : center;
	top        : 30px;
	width      : 120px;
}
