.clients {
	margin-top : -40px;
}

.clients--services {
	margin-top : 0;

	.clients__wrapper {
		padding : 25px 0;

		@media screen and (min-width : $break-table) {
			margin  : 0;
			padding : 40px 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.clients--media {
	margin-top : 0;

	.clients__wrapper {
		padding : 25px 0;

		@media screen and (min-width : $break-table) {
			margin  : 0;
			padding : 40px 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.swiper--clients {
	position : relative;
	width    : 100%;

	.swiper-wrapper {
		align-items : center;
	}

	.swiper-slide {
		margin     : 0 15px;
		text-align : center;
		width      : 30%;

		@media screen and (min-width : $break-table) {
			margin : 0 15px;
			width  : 20%;

			img {
				object-fit : cover;
				width      : 100%;
			}
		}

		@media screen and (min-width : $break-desktop) {
			margin : 0;
			width  : 16.5%;
			img {
				object-fit : unset;
				width      : auto;
			}
		}
	}

	.swiper-button-prev {

		@media screen and (min-width : $break-table) {
			margin-right : 50px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-right : 100px;
		}
	}

	.swiper-button-next {

		@media screen and (min-width : $break-table) {
			margin-left : 50px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 100px;
		}
	}
}

.clients__wrapper {
	align-items : center;
	display     : flex;
	margin      : 0 -15px;
	padding     : 50px 0 25px 0;

	@media screen and (min-width : $break-table) {
		margin  : 0;
		padding : 75px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-button {
		display : none;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

}

.tiny-text--clients {
	color : $white;
}


