.cta {
	padding : 80px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
	}
}

.cta__title {
	color         : $black;
	font-size     : 30px;
	font-weight   : 600;
	line-height   : 36px;
	margin-bottom : 32px;
	max-width     : 220px;

	@media screen and (min-width : $break-table) {
		font-size   : 54px;
		line-height : 64px;
		max-width   : 484px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 64px;
		line-height   : 72px;
		margin-bottom : 64px;
	}
}

.cta__logo {
	display   : block;
	left      : 100%;
	position  : relative;
	transform : translateX(-100%);
}
