.hero {
	align-items         : center;
	background-color    : $blue;
	background-image    : url("../images/home/hero/hero-bg.jpg");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	display             : flex;
	flex-direction      : column;
	min-height          : 100vh;

	&--home {
		min-height: 768px;
		height: 100vh;
		overflow: hidden;


		@media screen and (max-width: 600px) {
			min-height: 500px;
		}
	}
}

.hero__wrapper {
	width  				  : 100%;
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	justify-content : center;
	min-height      : 100vh;
	padding-bottom  : 80px;
	padding-top     : 79px;
	text-align      : center;
	@media screen and (min-width : $break-table) {
		padding-bottom : 80px;

	}


	@media screen and (min-width : $break-desktop) {
		padding-bottom : 0;
		padding-top    : 230px;
	}

	@media screen and (min-width : 1140) {
		padding-bottom : 0;
		padding-top    : 190px;
	}
}


.hero__title {
	color       : $white;
	font-family : 'ChakraPetch';
	font-size   : 30px;
	font-weight : 600;
	line-height : 36px;
	margin      : 0 auto 120px auto;
	max-width   : 309px;

	@media screen and (min-width : $break-table) {
		font-size   : 54px;
		line-height : 64px;
		margin      : 0 auto 160px auto;
		max-width   : 556px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 72px;
		margin      : 0 auto 100px auto;
		max-width   : 955px;
	}

	.wrap {
		color : $white;
	}
}

.hero__title--first {
	margin     : 0 auto 120px auto;
	min-height : 75px;

	@media screen and (min-width : $break-table) {
		margin     : 0 auto 100px auto;
		min-height : 135px;
	}

	@media screen and (min-width : $break-desktop) {
		margin     : 0 auto 120px auto;
		min-height : 155px;
	}
}

.hero__title--second {
	margin     : 0 auto 120px auto;
	min-height : 111px;

	@media screen and (min-width : $break-table) {
		margin     : 0 auto 100px auto;
		min-height : 199px;
	}

	@media screen and (min-width : $break-desktop) {
		margin     : 0 auto 120px auto;
		min-height : 155px;
	}
}

.hero__title--third {
	margin     : 0 auto 120px auto;
	min-height : 108px;

	@media screen and (min-width : $break-table) {
		margin     : 0 auto 100px auto;
		min-height : 199px;
	}

	@media screen and (min-width : $break-desktop) {
		margin     : 0 auto 40px auto;
		min-height : 227px;
	}

	@media screen and (min-width : 1450) {
		margin     : 0 auto 120px auto;
		min-height : 227px;
	}
}


.hero__title--services {
	@media screen and (min-width : $break-table) {
		max-width : 556px;
	}

	@media screen and (min-width : $break-desktop) {

		max-width : none;
	}

	a {
		color : $white;
	}
}

.hero__title--media {
	margin : 0 auto;

	@media screen and (min-width : $break-desktop) {
		margin : 0 auto 100px auto;
	}
}

.hero__wrap {
	display         : flex;
	justify-content : center;
	width           : 100%;
}

.hero__content {
	position: relative;
}
.hero__item {
	display         : flex;
	flex-direction  : column;
	justify-content : space-between;
	width 		      : 100%;
	height          : 100%;
	opacity         : 0;
	pointer-events  : none;
	// transition      : visibility .6s ease, opacity .6s ease;
	visibility      : hidden;
	position        : absolute;
	top             : 0; 
	left            : 0;

	&:nth-child(3) {
		position			: static;
	}

	&.active {
		opacity         : 1;
		visibility      : visible;
		pointer-events  : auto;

		//.hero__title--hero {
		//	opacity    : 1;
		//	visibility : visible;
		//}

		.hero__svg {
			opacity    : 1;
			visibility : visible;
		}
	}
}

.hero__item--main {
	&.active {
		display         : flex;
		flex-direction  : column;
		height          : 100%;
		justify-content : space-between;
		opacity         : 1;
		visibility      : visible;

		//.hero__title--hero {
		//	opacity    : 1;
		//	visibility : visible;
		//}

		.hero__svg {
			opacity    : 1;
			visibility : visible;
		}
	}
}

.hero__img {
	display: flex;
	justify-content: center;
	text-align : center;
	width      : 100%;

	img {
		max-width : none;
	}
}

.hero__images {
	height : 90px;

	@media screen and (min-width : $break-table) {
		height : 200px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.hero__svg {
	height     : 90px;
	opacity    : 0;
	visibility : hidden;
	width      : 900px;
	flex-shrink: 0;
	@media screen and (min-width : $break-table) {
		height : 200px;
		width  : 1600px;
	}

	@media screen and (min-width : $break-desktop) {
		width : auto;
	}

}

.hero__svg--one {
	path {

		stroke-dasharray  : 8931.775390625;
		stroke-dashoffset : 8931.775390625;
	}
}

.hero__svg--two {
	width : 723px;

	@media screen and (min-width : $break-table) {
		width : 1420px;
	}

	@media screen and (min-width : $break-desktop) {
		width : auto;
	}

	path {
		stroke-dasharray  : 11152.3486328125;
		stroke-dashoffset : 11152.3486328125;
	}
}

.hero__svg--three {
	path {
		stroke-dasharray  : 7533.6484375;
		stroke-dashoffset : 7533.6484375;
	}
}

@keyframes lineanim {
	100% {
		stroke-dashoffset : 0;
	}
}

.svg-animate {
	path {
		animation : lineanim 4s ease forwards;
	}
}

.hero__item.active .typed-cursor{
	animation: typedjsBlink 0.7s infinite;
	animation-delay: 3.5s;
}
@keyframes typedjsBlink {
	50% {
		opacity: 0.0;
	}
}

.hero__title {
	position: relative;
}
.hero__title-wrapper {
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.title-hide {
	opacity: 0;
	pointer-events: none;
}
