.about {
	background-image  : url("../images/home/about/about-bg.svg");
	background-repeat : no-repeat;
	background-size   : cover;
	padding-top       : 120px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about__wrapper {
	margin-bottom : 80px;

	@media screen and (min-width : $break-table) {
		text-align : center;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin    : 0 auto 24px auto;
		max-width : 1024px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about__title-name {
	text-transform: uppercase;
}

.about__text {
	color         : $black;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		margin    : 0 auto 40px auto;
		max-width : 440px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.record {
	position : relative;
	z-index  : 1;
}

.record__main {
	background-color : $white;
	border-radius    : 8px;
	box-shadow       : 0 8px 16px 0 rgba(0, 0, 0, 0.08), 0 0 4px 0 rgba(0, 0, 0, 0.04);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.record__wrapper {
	padding : 32px 32px;

	@media screen and (min-width : $break-table) {
		padding : 48px 48px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 80px;
	}
}

.record__top {
	display               : grid;
	grid-gap              : 40px 48px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 64px;

	@media screen and (min-width : $break-table) {
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 80px;
	}
}

.record-item {
	grid-column : 2 span;
	width       : auto;

	&:last-child {
		grid-column : 2/4;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.record__tiny-text {
	margin-bottom : 16px;
}

.record-item__wrap {
	align-items     : center;
	display         : flex;
	height          : 120px;
	justify-content : center;
	line-height     : 36px;
	margin          : 0 auto 16px auto;
	position        : relative;
	width           : 120px;

	@media screen and (min-width : $break-table) {
		height : 70px;
		width  : 70px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 120px;
		width  : 120px;
	}

	svg {
		left      : 50%;
		position  : absolute;
		top       : 50%;
		transform : translate(-50%, -50%);
	}


}

.record-item__container {
	left      : 50%;
	position  : absolute;
	top       : 50%;
	transform : translate(-50%, -56%);
	width: 120px ;
	height: 120px;

	@media screen and (min-width : $break-table) {
		transform: translate(-29%,-34%);
		width: 120px;
		height: 120px;
	}

	@media screen and (min-width : $break-desktop) {
		width: 120px;
		height: 120px;
		transform : translate(-50%, -56%);
	}

	canvas {
		width: 120px !important;
		height: 120px !important;
		@media screen and (min-width : $break-table) {
			width: 70px !important;
			height: 70px !important;
		}

		@media screen and (min-width : $break-desktop) {
			width: 120px !important;
			height: 120px !important;
		}
	}
}

.record-item__num {

	color       : $black;
	font-size   : 30px;
	font-weight : 600;


	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 48px;
	}

}

.record-item__text {
	color       : $black;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 18px;
		line-height : 24px;
	}
}

.record__bottom {
	display               : grid;
	grid-gap              : 48px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 25px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 32px;
	}
}

.record-num {
	grid-column : 4 span;
}

.record-num__number {
	background-color : rgba(255, 255, 255, 0.00);
	border           : 0.5px solid $black;
	border-radius    : 10px;
	box-shadow       : 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
	color            : $black;
	font-size        : 30px;
	font-weight      : 600;
	line-height      : 36px;
	margin-bottom    : 16px;
	padding          : 24px 28px;
	text-align       : center;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 48px;
	}
}

.record-num__text {
	color          : $black;
	font-size      : 14px;
	font-weight    : 500;
	letter-spacing : 1.26px;
	line-height    : 20px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}
}

.about-gradient {
	background-image    : url("../images/home/about/about-bg-gradient-mob.jpg");
	background-position : bottom center;
	background-repeat   : no-repeat;
	background-size     : cover;
	margin-top          : -450px;
	padding             : 315px 0;


	@media screen and (min-width : $break-table) {
		background-image    : url("../images/home/about/about-bg-gradient.jpg");
		background-position : center center;
		margin-top          : -150px;
		padding             : 140px 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : -250px;
		padding    : 230px 0;
	}
}

.count {
	overflow : visible;
}

.line {
	transform  : translate(0, 0) scale(1);
	transition : transform 0.3s ease-in;
}

.line--one {


	&.active {
		transform : translate(-5px, -5px) scale(1.1);
	}
}

.line--two {
	&.active {
		transform : translate(-6px, -5px) scale(1.1);
	}
}

.line--three {
	&.active {
		transform : translate(-5px, -5px) scale(1.1);
	}
}

.line--four {
	&.active {
		transform : translate(-5px, -5px) scale(1.1);
	}
}
