.team-item {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.team-item__img {
	margin-bottom : 24px;
	position      : relative;

	&:before {
		background-image  : url("../images/icon/logo-crop.png");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 24px;
		left              : 16px;
		position          : absolute;
		top               : 16px;
		width             : 40px;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.team-item__name {
	color          : $black;
	font-size      : 12px;
	font-weight    : 600;
	letter-spacing : 1.5px;
	line-height    : 133%;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	span {
		display : block;
	}
}

.team-item__about {
	color       : $black;
	font-size   : 12px;
	font-weight : 400;
	line-height : 133%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 142%;
	}
}
