.about-slider {
	padding-top : 80px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-top : 228px;
	}
}

.about-slider__content {
	margin         : 0 10px;
	padding-bottom : 33px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 40px;
	}
}

.about-slider__head {
	counter-reset         : count;
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);
	padding-bottom        : 24px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px 32px;
		grid-template-columns : repeat(6, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
}

.about-slider__button {
	align-items      : flex-start;
	background-color : transparent;
	border           : none;
	color            : $black;
	display          : flex;
	font-size        : 18px;
	font-weight      : 600;
	grid-column      : 4 span;
	line-height      : 133%;
	padding          : 0;
	position         : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:nth-child(2) {
		@media screen and (min-width : $break-table) {
			grid-row : 2/2;
		}

		@media screen and (min-width : $break-desktop) {
			grid-row : unset;
		}
	}

	&:after {
		background-image  : url("../images/icon/arrow-btn.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 24px;
		position          : absolute;
		right             : 0;
		top               : 50%;
		transform         : translateY(-50%);
		width             : 24px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : none
		}
	}

	&:before {
		color             : rgba(0, 0, 0, 0.5);
		content           : counter(count);
		counter-increment : count;
		font-size         : 12px;
		font-weight       : 600;
		letter-spacing    : 1.5px;
		line-height       : 133%;
		padding-right     : 8px;
		position          : relative;
		text-transform    : uppercase;
		top               : 2px;
		z-index           : 1;
	}
}

.about-slider__wrapper {
	background-image    : url("../images/about/about-tabs/about-desktop.jpg");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 24px 0 48px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin  : 0 -40px;
		padding : 122px 0;
	}
}

.about-slider__top {
	margin-bottom : -14px;
	position      : relative;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}

	img {
		max-width      : 100%;
		min-height     : 110px;
		vertical-align : unset;
	}
}

.about-slider__wrap {
	left     : 0;
	position : absolute;
	top      : 17px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		top : 0px;
	}

	.tiny-text {
		color         : $white;
		margin-bottom : 9px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 18px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 24px;
		}
	}
}

.about-slider__name {
	color       : $white;
	font-size   : 22px;
	font-weight : 600;
	line-height : 136%;

	@media screen and (min-width : $break-table) {
		font-size   : 22px;
		line-height : 125%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 116%;
	}
}

.about-slider__text {
	color       : $white;
	font-size   : 14px;
	font-weight : 400;
	line-height : 142%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size : 18px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 24px;
			}
		}
	}

	&:not(:last-child) {
		margin-bottom : 32px;
	}
}

.about-slider__text--center {
	text-align : start;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		text-align : center;
	}
}

.about-slider__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
		margin-top            : 54px;
	}
}

.about-slider__item {
	border        : 1px solid $white;
	border-radius : 10px;
	grid-column   : 4 span;
	padding       : 16px 16px;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 32px 32px;
	}
}


.about-slider__title {
	color          : #FFF;
	font-size      : 20px;
	font-weight    : 600;
	letter-spacing : 0.48px;
	line-height    : 128%;
	margin-bottom  : 16px;
	text-align     : left;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 133%;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
		text-align    : center;
	}
}

.about-slider__title--relation {
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin    : 0 auto 32px auto;
		max-width : 180px;
	}
}


.modal-btn {
	align-items      : center;
	background-color : transparent;
	border           : 1px solid $white;
	border-radius    : 10px;
	color            : $white;
	display          : flex;
	font-size        : 20px;
	font-weight      : 600;
	grid-column      : 4 span;
	justify-content  : space-between;
	line-height      : 120%;
	padding          : 16px 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		font-weight : 600;
		grid-column : 2 span;
		line-height : 133%;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
		padding     : 16px 20px;
	}

	&:after {
		background-image  : url("../images/icon/arrow-top.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		height            : 32px;
		width             : 32px;
	}
}

.swiper--about {
	margin : 0 -10px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-slide {
		width : 100%;
	}
}
