.contacts {
	background-image    : url("../images/about/about/about-bg.svg");
	background-position : top center;
	background-repeat   : no-repeat;
	padding             : 48px 0 56px 0;
	margin-top: 79px;
	@media screen and (min-width : $break-table) {
		margin-top: 94px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 120px 0;
		margin-top: 123px;
		background-size     : 100%;
	}
}

.contacts__grid {
	display               : grid;
	grid-row-gap          : 60px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-row-gap          : 80px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 0;
	}
}

.contacts__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5 span;
		grid-row    : 1/3;
	}
}

.contacts__right {
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-column           : 12 span;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items           : flex-start;
		grid-column           : 7/13;
		grid-template-columns : repeat(6, 1fr);
	}
}

.tiny-text--contacts {
	color         : #808080;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.form--contacts {
	margin-top : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 48px;
	}

	.b-btn--blue {
		margin-top : 24px;
		padding    : 16px 44px;
		width      : 100%;

		@media screen and (min-width : $break-table) {
			width : auto;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.contacts__map {
	border-radius : 10px;
	margin-bottom : 24px;
	min-height    : 234px;
	order         : -1;
	overflow      : hidden;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		grid-row    : 2/2;
		min-height  : 360px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 4/7;
		grid-row      : 2/2;
		margin-bottom : 0;
		min-height    : 234px;
	}
}

.contacts__title {
	margin-bottom : 24px;
	order         : -1;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		grid-row    : 1/1;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 6 span;
		grid-row      : 1/1;
		margin-bottom : 72px;
	}
}


.contacts__subtitle {
	color         : #808080;
	margin-bottom : 8px;
}

.contacts__list {
	li {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}
}

.contacts__list--top {
	border-bottom  : 1px solid #CCC;
	padding-bottom : 24px;

	@media screen and (min-width : $break-table) {
		border         : none;
		grid-column    : 1/5;
		padding-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/4;
		grid-row    : 2/2;
	}
}

.contacts__list--bottom {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}

	li {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 48px;
			}
		}
	}
}

.contacts__link {
	color       : $black;
	font-size   : 20px;
	font-weight : 600;
	line-height : 120%;


	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 125%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
	}
}

.company-name {
	color         : $black;
	font-size     : 20px;
	font-weight   : 600;
	line-height   : 120%;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 133%;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.contacts__bottom {
	padding-top : 24px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 6/13;
		padding-top : 0;
	}

	@media screen and (min-width : $break-desktop) {
		border-top  : 1px solid #CCC;
		grid-column : 6 span;
		grid-row    : 3/3;
		margin-top  : 87px;
		padding-top : 87px;
	}
}

.contacts__text {
	color       : #707070;
	font-size   : 12px;
	font-weight : 400;
	line-height : 133%;
	max-width   : 326px;

	@media screen and (min-width : $break-table) {
		max-width : none;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 150%;
	}
}
