.overlay {
	background : rgba(0, 0, 0, 0.85);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	top        : 0;
	//z-index: -1;
	transition : opacity .3s ease, visibility .3s ease;
	visibility : hidden;
	z-index    : 101;

	&.active {
		opacity    : 1;
		visibility : visible;
		/*  z-index: 9;
		transition: opacity 0.5s ease; */
	}
}

.popup {
	left       : 50%;
	max-width  : 320px;
	opacity    : 0;
	//z-index: -3;
	padding    : 0 15px;
	position   : fixed;
	top        : 50%;
	transform  : translate(-50%, -50%);
	transition : opacity 0.5s ease, visibility .5s ease;
	visibility : hidden;
	width      : 100%;
	z-index    : 103;


	&.active {
		opacity    : 1;
		visibility : visible;
		//transform: translate(-50%, -45%);
		z-index    : 103;
	}

	&__wrap {
		align-items      : center;
		background-color : $white;
		border-radius    : 4px;
		display          : flex;
		flex-direction   : column;
		justify-content  : center;
		min-height       : 220px;
		overflow         : hidden;
		padding          : 44px 8px;
		position         : relative;

		@media screen and (min-width : $break-table) {
			padding : 44px 44px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 64px 72px;
		}
	}

	&__close {
		background-color : transparent;
		border           : none;
		height           : 32px;
		padding          : 0;
		position         : absolute;
		right            : 14px;
		top              : 0px;
		width            : 32px;
		z-index          : 999;


		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			right : 24px;
			top   : 12px;
		}
	}
}

.popup__wrap {
	width : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.popup--about {
	max-height : 100%;
	max-width  : 377px;
	overflow-y : auto;
	padding    : 0 15px;
	top        : 0;
	transform  : translateX(-50%);


	@media screen and (min-width : $break-table) {
		max-height : 550px;
		max-width  : 704px;
		top        : 50%;
		transform  : translate(-50%, -50%);

		&::-webkit-scrollbar {

		}

		&::-webkit-scrollbar {
			background-color : rgba(170, 170, 170, 0.51);
			height           : 8px;
			width            : 5px;
		}

		&::-webkit-scrollbar-thumb {
			background    : $white;
			border-radius : 10px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		max-height : 580px;
		min-width  : 1126px;
	}
}

.popup--work {
	max-width: 100%;
	max-height: 100vh;
	overflow-y: auto;

	.popup__wrap {
		max-width: 1272px;
		margin-top: 15px;
		margin-bottom: 40px;
		margin-left: auto;
		margin-right: auto;
	}

	.popup__close {
		right: 4px;
	}

	&::-webkit-scrollbar {
		background-color : rgba(170, 170, 170, 0.51);
		height           : 8px;
		width            : 5px;
	}

	&::-webkit-scrollbar-thumb {
		background    : $white;
		border-radius : 10px;
	}

	@media screen and (min-width : $break-desktop) {
		.popup__wrap {
			margin-top: 80px;
		}

		.popup__close {
			right: 14px;
		}
	}
}

.modal {
	background-color : #FFF;
	border-radius    : 10px;
}

.modal__top--about {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	padding-bottom  : 28px;
	position        : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 64px;
	}

}

.modal__title--about {
	color       : $black;
	font-size   : 20px;
	font-weight : 600;
	line-height : 120%;
	position    : relative;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 125%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size : 40px;
	}

	&:after {
		background-color : $black;
		bottom           : -16px;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		width            : 100%;

		@media screen and (min-width : $break-table) {
			bottom : -16px;
		}

		@media screen and (min-width : $break-desktop) {
			bottom : -26px;
		}
	}
}

.modal__icon--about {
	height : 24px;
	width  : 44px;

	@media screen and (min-width : $break-desktop) {
		height : 45px;
		width  : 82px;
	}
}

.modal__wrapper {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.modal__item--about {
	&:not(:last-child) {
		margin-bottom : 24px;
	}
}

.modal__name--about {
	color         : $black;
	font-size     : 18px;
	font-style    : normal;
	font-weight   : 600;
	line-height   : 133%;
	margin-bottom : 8px;
}

.modal__text {
	color       : $black;
	font-size   : 12px;
	font-weight : 400;
	line-height : 133%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 142%;
	}
}

.modal__list {
	margin       : 0;
	padding-left : 20px;

	li {
		color       : $black;
		font-size   : 12px;
		font-weight : 400;
		line-height : 133%;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 142%;
		}
	}
}

.article {
	h3 {
		margin-bottom: 16px;
	}

	h4 {
		font-size: 12px;
		margin-bottom: 16px;
		text-transform: uppercase;
		letter-spacing: 1.5px;
		font-weight: 600;
		opacity: 0.6;
		margin-top: 32px;
	}
	strong {
		font-weight: 600;
	}

	p {
		line-height: 140%;
		margin-bottom: 15px;
		font-size: 14px;
		font-weight: 400;

		&:last-child {
			margin-bottom: 0;
		}
	}
	blockquote {
		display: block;
		padding: 20px;
		border: 1px solid $blue;
		border-radius: 30px 30px 30px 0px;
		color: $blue;
		position: relative;
		margin: 30px 0 0 14px;

		&:after {
			display: none;
		}

		p {
			color: $blue;
		}

		&:before {
			content: '';
			display: block;
			width: 48px;
			height: 43px;
			position: absolute;
			top: -19px;
			left: -18px;
			z-index: 1;
			background-size: cover;
			background-position: top center;
			background-repeat: no-repeat;
			background-image: url('../images/ourwork/popups/arrow.svg');
		}
	}

	@media screen and (min-width : $break-table) { 
		blockquote {
			padding: 32px;
		}
		p {
			font-size: 16px;
		}

		blockquote p {
			font-size: 14px;
		}

	}

	@media screen and (min-width : $break-desktop) {
		blockquote {
			margin-left: 97px;
			margin-top: 55px;

			&:before {
				width: 128px;
				height: 112px;
				left: -97px;
				top: -30px;
			}
		}
		h3 {
			font-size: 40px;
			margin-bottom: 35px;
		}
	}
}

.article__list p {
	margin-bottom: 5px;
	&:last-child {
		margin-bottom: 0;
	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom: 8px;
		font-size: 14px;
	}
}


.article__picture {
	display: block;
	margin: 0 -8px;
	width: calc(100% + 16px);

	@media screen and (min-width : $break-table) { 
		margin: 0 -44px;
		width: calc(100% + 88px);
	}

	@media screen and (min-width : $break-table) { 
		margin: 0 -72px;
		width: calc(100% + 144px);
	}
}

.article__part {
	@media screen and (min-width : $break-table) {
		display: flex;
		justify-content: space-between;
	}
}

.article__data {
	@media screen and (min-width : $break-table) { 
		min-width: 220px;
		margin-right: 15px;
		flex-shrink: 0;
	}
}

.article__desc {
	@media screen and (min-width : $break-desktop) {
		max-width: 672px;
	}
}


.fade-out {
	animation : fadeOut 2s forwards;
}

@keyframes fadeOut {
	from {
		opacity : 0;
	}
	to {
		opacity : 1;
	}
}


