.testimonials {
	background-color    : $blue;
	background-image    : url(../images/home/hero/hero-bg.jpg);
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 80px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top     : -190px;
		padding-bottom : 120px;
		padding-top    : 281px;
	}
}

.testimonials__head {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 50px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 90px;
	}

	.swiper-nav {
		display : none;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		.swiper-button + .swiper-button {
			margin-left : 24px;
		}
	}
}

.testimonials__wrap {
	align-items     : flex-start;
	display         : flex;
	justify-content : space-between;
}

.tiny-text--testimonials {
	color         : $white;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}
}

.testimonials__title {
	color : $white;

	@media screen and (min-width : $break-table) {
		max-width : 492px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 715px;
	}
}

.testimonials-item {
	border         : 1px solid $white;
	border-radius  : 50px 50px 50px 0;
	display        : flex;
	flex-direction : column;
	margin-left    : 14px;
	margin-right   : 14px;
	margin-top     : 25px;
	padding        : 24px 24px;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 110px;
		margin-top  : 25px;
		padding     : 32px 32px;
	}

	&:before {
		background-image  : url("../images/icon/quotes.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 42px;
		left              : -15px;
		position          : absolute;
		top               : -20px;
		width             : 48px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			height : 112px;
			left   : -100px;
			top    : -25px;
			width  : 128px;
		}
	}
}

.testimonials-item__name {
	color         : $white;
	font-size     : 18px;
	font-weight   : 600;
	line-height   : 24px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.testimonials-item__text {
	color         : $white;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 24px;
}

.testimonials-item__bottom {
	color       : $white;
	font-size   : 14px;
	font-weight : 600;
	line-height : 20px;
	margin-top  : auto;
	max-width   : 305px;
}

.swiper--testimonials {
	.swiper-slide {
		display : flex;
		height  : auto;
		width   : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-pagination {
		margin-top : 24px;
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}
