
.accordion {
	background-color : $white;
	border-radius    : 8px;
	padding-top      : 32px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding-top : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:not(:last-child) {
		border-bottom : 1px solid rgba(0, 0, 0, 0.2);
	}

	&:last-child {
		border-bottom : 1px solid rgba(0, 0, 0, 0.2);
	}
}

.accordion__control {
	align-items      : center;
	background-color : transparent;
	border           : none;
	cursor           : pointer;
	display          : flex;
	justify-content  : space-between;
	padding          : 0 0 32px 0;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		padding : 0 0 48px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.accordion.open {
	.accordion__content {
		grid-template-rows : 1fr;
		padding-bottom     : 24px;

		@media screen and (min-width : $break-table) {
			padding-bottom : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.accordion--services.open {

}


.accordion__content {
	display            : grid;
	grid-template-rows : 0fr;
	transition         : grid-template-rows 500ms, margin-top 500ms;
}

.accordion__main {
	overflow : hidden;
}

.accordion__icon {
	height     : 20px;
	position   : relative;
	transition : transform .3s ease;

	width      : 20px;

	&:after {
		background-color : #1D2620;
		content          : "";
		height           : 2px;
		position         : absolute;
		right            : 0;
		top              : 50%;
		transform        : translateY(-50%);
		width            : 18px;
	}

	&:before {
		background-color : #1D2620;
		content          : "";
		height           : 18px;
		position         : absolute;
		right            : 8px;
		top              : 50%;
		transform        : translateY(-50%) rotate(180deg);
		transition       : transform .3s ease;
		width            : 2px;
	}
}

.accordion.open .accordion__icon {
	transition : transform .3s ease;

	&:before {
		transform : translateY(-50%) rotate(90deg);
	}
}

.accordion__title {
	color       : $black;
	font-size   : 18px;
	font-weight : 600;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 32px;
		line-height : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion--item {
	background-color : transparent;
	border-radius    : 0;
	padding          : 32px 0;

	&:first-child {
		border-top : 1px solid $white;

		@media screen and (min-width : $break-desktop) {
			border-top : none;
		}
	}

	&:not(:last-child) {
		border-bottom : 1px solid $white;

		@media screen and (min-width : $break-desktop) {
			border-bottom : none;
		}
	}

	&:last-child {
		border-bottom : 1px solid $white;

		@media screen and (min-width : $break-desktop) {
			border-bottom : none;
		}
	}

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;

		&:not(:last-child) {
			margin-bottom : 80px;
		}
	}

	.accordion__control {
		padding : 0;

		@media screen and (min-width : $break-desktop) {
			pointer-events : none;
		}

		.accordion__title {
			color       : $white;
			font-size   : 18px;
			font-weight : 600;
			line-height : 133%;

			@media screen and (min-width : $break-table) {
				font-size   : 24px;
				line-height : 125%;
			}

			@media screen and (min-width : $break-desktop) {
				line-height : 133%;
			}
		}

		.accordion__icon {
			@media screen and (min-width : $break-desktop) {
				display : none;
			}

			&:after {
				background-color : $white;
			}

			&:before {
				background-color : $white;
			}
		}
	}

	&.open {
		.accordion__content {
			grid-template-rows : 1fr;
			padding-bottom     : 0;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	.accordion__main {

		@media screen and (min-width : $break-desktop) {
			overflow : visible;
		}
	}
}

.accordion--black {
	&:first-child {
		border-top : 1px solid $black;

		@media screen and (min-width : $break-desktop) {
			border-top : none;
		}
	}

	&:not(:last-child) {
		border-bottom : 1px solid $black;

		@media screen and (min-width : $break-desktop) {
			border-bottom : none;
		}
	}

	&:last-child {
		border-bottom : 1px solid $black;

		@media screen and (min-width : $break-desktop) {
			border-bottom : none;
		}
	}

	@media screen and (min-width : $break-table) {
		padding : 48px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;

		&:not(:last-child) {
			margin-bottom : 80px;
		}
	}

	.accordion__control {

		.accordion__title {
			color : $black;
		}

		.accordion__icon {

			&:after {
				background-color : $black;
			}

			&:before {
				background-color : $black;
			}
		}
	}
}
